@tailwind base;
@tailwind components;
@tailwind utilities;

.home-bg {
  background: url('./assets/game.jpg');
}

@keyframes scaleAnimation {
  0% {
    transform: scale(1);
    opacity:1;
  }
  50% {
    transform: scale(1.1);
    oapcity:0.7;
  }
  100% {
    transform: scale(1);
    opacity:0.5;
  }
}

.animate-scale {
  animation: scaleAnimation 2s infinite;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.card.is-flipped .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card-front {
  background-color: #e0e0e0;
  background-color: #d7b899 !important; /* Fallback color */
  background-image: 
    linear-gradient(45deg, #d7b899 25%, transparent 25%, transparent 75%, #d7b899 75%, #d7b899),
    linear-gradient(45deg, #d7b899 25%, transparent 25%, transparent 75%, #d7b899 75%, #d7b899) !important;
}

.card-back {
  background-color: #ffcd9a;
  transform: rotateY(180deg);
}
.bg-game-cover {
  background: url('./assets/game2.jpg');
}
